.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.orbit-gizmo {
  width: 500px;
  /* ABSOLUTE HERE */
  height: 500px;
  /* ABSOLUTE HERE */
  position: absolute;
  top: calc(50% - 250px);
  /* ABSOLUTE HERE */
  left: calc(50% - 250px);
  /* ABSOLUTE HERE */
  pointer-events: none; }

div.orbit-gizmo div {
  border: 0px;
  border-color: #989898;
  border-style: solid; }

.orbit-gizmo .layout-ver {
  width: 10%;
  height: 100%;
  float: left; }

.orbit-gizmo .layout-hor {
  width: 80%;
  height: 10%;
  margin: 0 auto; }

.orbit-gizmo .layout-mid {
  width: 100%;
  height: 80%;
  text-align: center; }

.orbit-gizmo .outside {
  width: 1000%;
  /* NOP_NOTE: technically should use vw and vh, caniuse.com/#feat=viewport-units */
  height: 1000%;
  margin-top: calc(100% / 2 - 1000% / 2);
  margin-bottom: calc(-100% / 2 - 1000% / 2);
  margin-left: calc(100% / 2 - 1000% / 2);
  cursor: url(data:image/png;base64,), auto;
  pointer-events: auto; }

.orbit-gizmo .ring {
  width: 80%;
  height: 80%;
  margin-bottom: -100%;
  border-width: 50px;
  /* ABSOLUTE HERE */
  border-style: solid;
  border-radius: 2500px;
  border-color: rgba(255, 255, 255, 0);
  cursor: url(data:image/png;base64,) 16 16, nesw-resize;
  pointer-events: auto; }

.orbit-gizmo .ring:hover {
  border-color: rgba(255, 255, 255, 0.1); }

.orbit-gizmo .circle {
  width: calc(80% - 3px);
  height: calc(100% - 3px);
  border-radius: 2500px;
  border-width: 1px;
  float: left;
  cursor: url(data:image/png;base64,), auto;
  pointer-events: auto; }

.orbit-gizmo .circle:hover {
  /*background-color: rgba(255,255,255,0.2);*/ }

.orbit-gizmo .circle .crosshair-area {
  width: 10%;
  height: 10%;
  margin: 0 auto;
  margin-top: 45%; }

.orbit-gizmo .circle .crosshair-area .crosshair-v {
  width: 0;
  height: 100%;
  margin: 0 auto;
  border-left-width: 1px; }

.orbit-gizmo .circle .crosshair-area .crosshair-h {
  width: 100%;
  height: 0;
  margin-top: -50%;
  border-bottom-width: 1px; }

.orbit-gizmo .edgemark-area {
  pointer-events: auto; }

.orbit-gizmo .edgemark-area .edgemark {
  pointer-events: none; }

.orbit-gizmo .edgemark-area:hover {
  background-color: rgba(255, 255, 255, 0.1); }

.orbit-gizmo .layout-hor .edgemark-area {
  width: 12.5%;
  /* square, w/8 ratio is 8 */
  height: 100%;
  margin: 0 auto;
  cursor: url(data:image/png;base64,) 16 16, ns-resize; }

.orbit-gizmo .layout-ver .edgemark-area {
  width: 100%;
  height: 12.5%;
  /* square */
  margin-top: 350%;
  /* as percentage of width (messed up, don't ask) */
  cursor: url(data:image/png;base64,) 16 16, ew-resize; }

.orbit-gizmo .layout-hor .edgemark-area .edgemark {
  width: 0;
  height: 100%;
  margin: 0 auto;
  border-left-width: 1px; }

.orbit-gizmo .layout-ver .edgemark-area .edgemark {
  width: 100%;
  height: 0;
  padding-top: 50%;
  border-bottom-width: 1px; }
