.adsk-viewing-viewer .adsk-searchbox input.search-box {
  height: 34px;
  direction: ltr;
  margin: 0;
  padding: 7px 12px 8px 40px;
  font-size: 12px;
  width: 100%;
  outline: none;
  position: relative;
  /* Bootstrap Fix */
  box-sizing: border-box;
  line-height: normal;
  z-index: 1; }

.adsk-viewing-viewer .adsk-searchbox .search-box-icon {
  left: 12px;
  top: 10px;
  width: 13px;
  height: 13px;
  position: absolute;
  z-index: 1; }

.adsk-viewing-viewer .adsk-searchbox .search-box-close {
  right: 13px;
  top: 10px;
  width: 14px;
  height: 14px;
  cursor: pointer;
  position: absolute;
  z-index: 1; }

.adsk-viewing-viewer .adsk-searchbox.empty > .search-box-close {
  display: none; }
