.adsk-viewing-viewer .alert-box {
  font-family: "Helvetica Neue Light", "Segoe UI Semilight", sans-serif;
  display: block;
  position: absolute;
  z-index: 1;
  left: 50%;
  top: 50%;
  height: auto;
  max-width: 40%;
  background: rgba(250, 250, 250, 0.9);
  padding: 10px;
  visibility: hidden;
  border-radius: 5px;
  border-width: 1px;
  border-color: #b8b8b8;
  border-style: solid;
  white-space: -moz-pre-wrap;
  /* Mozilla */
  white-space: pre-line;
  /* CSS 3 (and 2.1 as well, actually) */
  word-wrap: break-word;
  /* IE */
  word-break: break-all;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%); }

.adsk-viewing-viewer .alert-box-image {
  display: block;
  float: left;
  left: 10px;
  top: 10px;
  width: 60px;
  height: 60px;
  padding-right: 10px; }

.adsk-viewing-viewer .alert-box-msg {
  display: block;
  color: #333333;
  margin-left: 70px;
  margin-top: 2px; }

.adsk-viewing-viewer .alert-box-title {
  display: block;
  word-wrap: break-word;
  word-break: break-all;
  font-size: 18px;
  min-height: 18px;
  margin-bottom: 10px; }

.adsk-viewing-viewer .alert-box-text {
  display: block;
  word-wrap: break-word;
  word-break: break-word;
  font-size: 12px;
  min-height: 12px; }

.adsk-viewing-viewer .alert-box-content {
  display: block;
  margin-top: 10px;
  font-size: 12px;
  min-height: 12px;
  list-style: disc; }

.adsk-viewing-viewer .alert-box-ok {
  display: block;
  position: relative;
  z-index: 2;
  width: 50px;
  height: auto;
  left: 50%;
  margin-left: -25px;
  margin-top: 10px;
  bottom: 5%;
  line-height: 20px;
  text-align: center;
  color: #000;
  font-size: 14px;
  background: rgba(0, 0, 0, 0);
  border-radius: 2px;
  border-width: 1px;
  border-color: #999999;
  border-style: solid;
  padding: 3px;
  cursor: pointer;
  -webkit-transition: all .2s ease-in-out;
  -moz-transition: all .2s ease-in-out;
  -ms-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out; }

.adsk-viewing-viewer .alert-box-ok:hover {
  color: #111111;
  background: white; }

/* Multiple errors display */
.adsk-viewing-viewer .alert-box.errors {
  max-height: 200px; }

.adsk-viewing-viewer .alert-box-msg.errors {
  max-height: 150px;
  padding-right: 15px;
  overflow-y: auto; }

.adsk-viewing-viewer .alert-box-title.errors {
  margin-top: 15px; }

.adsk-viewing-viewer .alert-box-text.errors {
  margin-top: 5px; }

